.Payment {
	display:grid;
    grid-template-columns: repeat(6 ,1fr);
    width: 100%;
}
.Payment p {
    font-family: Arial, Helvetica, sans-serif;
}
.Payment h3{
    font-size: 16px;
    font-family: Arial, Helvetica, sans-serif;
}
.Payment h2 {
    font-size: 22px;
    font-family: Arial, Helvetica, sans-serif;
}
.Payment .body{
    display: grid;
    grid-template-columns: repeat(6 ,1fr);
    grid-column: span 6;
    margin-top: 50px;

}
.Payment .footer{
    grid-column: span 6;
    margin-top: 50px;
}
.Payment .formPayment{
    grid-column: span 3;
    /* height: 400px; */
    margin-left: 30px;
}
.Payment .productInfo{
    grid-column: span 3;
    /* height: 400px; */
}
.Payment .productInfo img{
    width: 70px;
    height: 70px;
}
.Payment .title {
    grid-column: span 6;
    height: 200px;
    text-align: center;

}
.Payment .title img{
    width: 250px;
    height: 100%;
    margin: 10px;
    margin-top: 0px;
    border:1px solid whitesmoke;
    border-radius: 10px;
    text-align: center;
}
.Payment .city{
    width: 45%;
    margin-top: 20px;

}
.Payment .zone{
    width: 45%;
    margin-top: 20px;
    margin-right: 10%;
}
.Payment .productInfo .selectSizeCount{
    display: grid;
    grid-template-columns: repeat(6 ,1fr);
}
.Payment .productInfo .selectSize{
    grid-column: span 3;
}
.Payment .productInfo .selectCount{
    grid-column: span 3;
    
}
.Payment .productInfo .delivery{
    grid-column: span 6;
}
.productInfo .selectCount h2{
    width: 100%;
}
.selectCount button{
    display: inline-block;
    margin: 50px;
    margin-top: 20px;

}
.selectCount p {
    display: inline-block;
}
.Payment .footer .payMotheds{
    display: grid;
    grid-template-columns: repeat(6 ,1fr);
}
.Payment .footer  .cridt-card{
    grid-column:  span 2;

}
.Payment .footer  .paypal{
    grid-column:  span 2;
    

}
.Payment.footer  .payByPhone{
    grid-column:  span 2;
    margin: 5px;
}
.Payment .footer .cridt-card .user-id{
    width: 100%;
    justify-items: center;
}
.Payment .footer .cridt-card .user-card-num{
    width: 100%;
    margin-top: 5px;
}
.Payment .footer .cridt-card .user-card-expired{
    width: 40%;
    margin-top: 5px;
    justify-items: center;
}
.Payment .footer .cridt-card .user-cvv{
    width: 55%;
    justify-items: center;
    margin-right: 5%;
    margin-top: 5px;

}
.Payment .submit-all-data{
    grid-column: span 6;
    margin-top: 150px;
    text-align: center;
}
.Payment .submit-all-data button{
    width: 500px;
    background:#7F8C8D ;
}
.Payment .final-payment{
    display: grid;
    grid-template-columns: repeat(6 ,1fr);
    grid-column: span 6;
}
.Payment .final-payment .final-payment-child1{
    grid-column: span 4;
}
.Payment .final-payment .final-payment-child2{
    grid-column: span 2;
}



.Payment .footer .paypal .user-id2{
    width: 90%;
    margin-right: 10%;
}
.Payment .footer .paypal .user-card-num2{
    width: 90%;
    margin-right: 10%;
    margin-top: 5px;
}
.Payment .footer .paypal .user-card-expired2{
    width: 30%;
    margin-right: 10%;
    margin-top: 5px;
    justify-items: center;
}
.Payment .footer .paypal .user-cvv2{
    width: 55%;
    justify-items: center;
    margin-right: 5%;
    margin-top: 5px;

}
.Payment .payByPhone{
    justify-items: center;
}
.Payment .payByPhone .contact-phone {
    width: 80%;
    height: 80%;
    background-color:#EBEDEF ;
    text-align: center;
    margin-right: 20%;
    display: flex;
    flex-direction: column;
}