.Header{
    display: flex;
    width: 100%;
    background-color: #D5DBDB ;
}

.Header .logobttn img {
    height: 35px;
    width: 10%;
    border-radius: 3px;
}

.Header .logobttn {
    font-size: 40px;
    font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    width: 30%;
    
}
.Header .logobttn a{
    text-decoration:none;
    font-size: 40px;
    color: black;
}
.Header .searchbttn{
    width: 50%;
    
    
}
.Header .searchbttn input{
    margin-top: 15px;
}


.Header .loginbttn{
    width: 20%;
    text-align: center;
}
.Header .loginbttn a{
    font-size: 20px;
    text-decoration: none;
    color: black;
    margin: 30px;
    margin-top: 30px;
}