


.UseMenu nav ul {
	position: relative;
    list-style-type: none;
}
.UseMenu nav ul li {
    margin: 10px;
}
.UseMenu nav ul  a{
    text-decoration: none;
    color: black;
    font-size: 17px;
    display: flex;
    text-transform: capitalize;
}
.UseMenu nav ul li a:hover {
    color:#1ABC9C;
}
.UseMenu nav ul ul {
    display: none;
}
.UseMenu nav ul .DropMenu{
    position: relative;
}
.UseMenu ul .DropMenu:hover ul{
    display: initial;
}
.UseMenu nav ul ul  {
    width: 0px;
    right: 50px;


}
.UseMenu ul ul a {
    font-size: 15px;
}