.Layout {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: 50px 780px 70px;
    direction: rtl;
    height: 900px;
    width: 100%;
    overflow:hidden;

}


.Layout  header{
grid-column: span 6;
direction: rtl;

}

.Layout  footer{
    grid-column: span 6;
    
}

    

.Layout  main {
    grid-column: span 5;
  direction: rtl;
  overflow: auto;
  margin-top: 30px;

}
.Layout main::-webkit-scrollbar{
    display: none;
}
.Layout  aside{
    margin-top: 30px;
    grid-column: span 1;
    direction: rtl;
    background-color:#D5DBDB;



    
}
.Layout main .mainTitle {
    width: 90%;
    height: 50px;
    margin-right: 20px;
}